const initialState = {
  stats: {
    total_orders: 0,
    pending_payment_orders: 0,
    pending_config_orders: 0,
    confirmed_payment_orders: 0,
    confirmed_config_orders: 0,
    total_amount: 0,
    total_commission: 0,
    one_month_orders: 0,
    three_month_orders: 0,
    six_month_orders: 0,
    lifetime_orders: 0,
    one_month_percentage: 0,
    three_month_percentage: 0,
    six_month_percentage: 0,
    lifetime_percentage: 0,
  },
  orders: [],
  sales: [], // sales
  salesLinks: [],
  customers: [],
  pagination: {
    total: 0,
    page: 1,
    limit: 20,
    total_pages: 0,
  },
  loading: false,
  error: null,
};

const adminSlice = createSlice({
  name: 'admin',
  initialState,
  reducers: {
    clearError: (state) => {
      state.error = null;
    },
    setPagination: (state, action) => {
      state.pagination = { ...state.pagination, ...action.payload };
    },
  },
  extraReducers: (builder) => {
    builder
      // 
      .addCase(getStats.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getStats.fulfilled, (state, action) => {
        state.loading = false;
        //  { success: true, data: {...} } 
        state.stats = action.payload.data || action.payload;
      })
      .addCase(getStats.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // 
      .addCase(getSales.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getSales.fulfilled, (state, action) => {
        state.loading = false;
        //  { success: true, data: { sales: [...], pagination: {...} } } 
        const data = action.payload.data || action.payload;
        state.sales = data.sales || data || [];
        if (data.pagination) {
          state.pagination = data.pagination;
        }
      })
      .addCase(getSales.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // 
      .addCase(getAdminOrders.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getAdminOrders.fulfilled, (state, action) => {
        state.loading = false;
        //  { success: true, data: {...} } 
        const data = action.payload.data || action.payload;
        state.orders = data.orders;
        state.pagination = data.pagination;
      })
      .addCase(getAdminOrders.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // 
      .addCase(exportOrders.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(exportOrders.fulfilled, (state) => {
        state.loading = false;
      })
      .addCase(exportOrders.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // 
      .addCase(getSalesLinks.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getSalesLinks.fulfilled, (state, action) => {
        state.loading = false;
        //  { success: true, data: {...} } 
        state.salesLinks = action.payload.data || action.payload;
      })
      .addCase(getSalesLinks.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // 
      .addCase(updateAdminOrderStatus.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(updateAdminOrderStatus.fulfilled, (state, action) => {
        state.loading = false;
        // 
        const orderIndex = state.orders.findIndex(order => order.id === action.payload.data.orderId);
        if (orderIndex !== -1) {
          state.orders[orderIndex].status = action.payload.data.status;
        }
      })
      .addCase(updateAdminOrderStatus.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // 
      .addCase(getCustomers.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getCustomers.fulfilled, (state, action) => {
        state.loading = false;
        //  { success: true, data: { customers: [...], pagination: {...} } } 
        const data = action.payload.data || action.payload;
        state.customers = data.customers || data || [];
      })
      .addCase(getCustomers.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

export const { clearError, setPagination } = adminSlice.actions;
export default adminSlice.reducer; 