import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { authAPI } from '../../services/api';

// action
export const login = createAsyncThunk(
  'auth/login',
  async (credentials, { rejectWithValue }) => {
    try {
      const response = await authAPI.login(credentials);
      // tokenlocalStorage
      localStorage.setItem('token', response.data.token);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response?.data?.message || '');
    }
  }
);

// actiontoken
export const verifyToken = createAsyncThunk(
  'auth/verifyToken',
  async (_, { rejectWithValue }) => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        throw new Error('token');
      }
      const response = await authAPI.verifyToken();
      return response.data;
    } catch (error) {
      localStorage.removeItem('token');
      return rejectWithValue(error.response?.data?.message || 'token');
    }
  }
);

// action
export const logout = createAsyncThunk(
  'auth/logout',
  async () => {
    localStorage.removeItem('token');
    return null;
  }
);

const initialState = {
  admin: null,
  token: localStorage.getItem('token'),
  isAuthenticated: false, // token
  loading: false,
  error: null,
};

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    clearError: (state) => {
      state.error = null;
    },
    //  
    setAuthenticated: (state, action) => {
      state.isAuthenticated = action.payload;
      if (action.payload) {
        state.admin = { username: 'admin', name: '()' };
        state.token = localStorage.getItem('adminToken') || localStorage.getItem('token');
      }
    },
  },
  extraReducers: (builder) => {
    builder
      // 
      .addCase(login.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(login.fulfilled, (state, action) => {
        console.log('Redux: login.fulfilled ');
        console.log('Redux: action.payload:', action.payload);
        console.log('Redux: action.payload.data:', action.payload.data);
        
        state.loading = false;
        state.isAuthenticated = true;
        state.admin = action.payload.data.admin;
        state.token = action.payload.data.token;
        
        console.log('Redux: state.token:', state.token);
        
        // localStorageRedux
        localStorage.setItem('token', action.payload.data.token);
      })
      .addCase(login.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // token
      .addCase(verifyToken.pending, (state) => {
        state.loading = true;
      })
      .addCase(verifyToken.fulfilled, (state, action) => {
        state.loading = false;
        state.isAuthenticated = true;
        state.admin = action.payload.data.admin;
        state.token = localStorage.getItem('token'); // token
      })
      .addCase(verifyToken.rejected, (state) => {
        state.loading = false;
        state.isAuthenticated = false;
        state.admin = null;
        state.token = null; // token
        localStorage.removeItem('token'); // localStorage
      })
      // 
      .addCase(logout.fulfilled, (state) => {
        state.isAuthenticated = false;
        state.admin = null;
        state.token = null;
        // localStorage
        localStorage.removeItem('token');
      });
  },
});

export const { clearError, setAuthenticated } = authSlice.actions;
export default authSlice.reducer; 