import React, { useEffect, lazy, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { verifyToken } from './store/slices/authSlice';
import LoadingSpinner from './components/LoadingSpinner';

// 
const SalesPage = lazy(() => import('./pages/SalesPage'));
const PurchasePage = lazy(() => import('./pages/PurchasePage'));
const AdminLoginPage = lazy(() => import('./pages/AdminLoginPage'));
const AdminDashboardPage = lazy(() => import('./pages/AdminDashboardPage'));
const SalesReconciliationPage = lazy(() => import('./pages/SalesReconciliationPage'));
const AuthTestPage = lazy(() => import('./pages/AuthTestPage'));
const PrimarySalesPage = lazy(() => import('./pages/PrimarySalesPage'));
const PrimarySalesSettlementPage = lazy(() => import('./pages/PrimarySalesSettlementPage'));
const UnifiedSecondarySalesPage = lazy(() => import('./pages/UnifiedSecondarySalesPage'));

// 
const ProtectedRoute = ({ children }) => {
  const { isAuthenticated, loading } = useSelector((state) => state.auth);
  
  if (loading) {
    return <LoadingSpinner />;
  }
  
  return isAuthenticated ? children : <Navigate to="/admin" replace />;
};

function App() {
  const dispatch = useDispatch();
  const { token, loading } = useSelector((state) => state.auth);

  useEffect(() => {
    // tokentoken
    if (token) {
      dispatch(verifyToken());
    }
  }, [dispatch, token]); // tokentoken

  if (loading) {
    return <LoadingSpinner />;
  }

  return (
    <Router>
      <div className="App">
      {/*  -  */}
      <a 
        href="#main-content" 
        className="skip-nav"
        style={{
          position: 'absolute',
          top: '-40px',
          left: '6px',
          zIndex: 1000,
          padding: '8px 16px',
          backgroundColor: '#1890ff',
          color: 'white',
          textDecoration: 'none',
          borderRadius: '4px',
          transition: 'top 0.3s'
        }}
        onFocus={(e) => {
          e.target.style.top = '6px';
        }}
        onBlur={(e) => {
          e.target.style.top = '-40px';
        }}
      >
        
      </a>

        <Routes>
          {/*  -  */}
          <Route path="/admin" element={<Suspense fallback={<div>...</div>}><AdminLoginPage /></Suspense>} />
          
          {/*  -  */}
          <Route path="/sales" element={<Suspense fallback={<div>...</div>}><SalesPage /></Suspense>} />
          
          {/*  */}
          <Route path="/sales/commission" element={<Suspense fallback={<div>...</div>}><PrimarySalesSettlementPage /></Suspense>} />
          
          {/*  */}
          <Route path="/sales/settlement" element={<Suspense fallback={<div>...</div>}><SalesReconciliationPage /></Suspense>} />
          
          {/*  -  */}
          <Route path="/purchase/:linkCode" element={<Suspense fallback={<div>...</div>}><PurchasePage /></Suspense>} />
          
          {/*  */}
          <Route path="/sales-reconciliation" element={<Suspense fallback={<div>...</div>}><SalesReconciliationPage /></Suspense>} />
          
          {/*  */}
          <Route path="/primary-sales" element={<Suspense fallback={<div>...</div>}><PrimarySalesPage /></Suspense>} />
          
          {/*  */}
          <Route path="/primary-sales-settlement" element={<Suspense fallback={<div>...</div>}><PrimarySalesSettlementPage /></Suspense>} />
          
          {/*  -  */}
          <Route path="/secondary-registration/:linkCode" element={<Suspense fallback={<div>...</div>}><UnifiedSecondarySalesPage /></Suspense>} />
          
          {/*  - sales_code */}
          <Route path="/secondary-sales" element={<Suspense fallback={<div>...</div>}><UnifiedSecondarySalesPage /></Suspense>} />
          <Route path="/purchase" element={<Suspense fallback={<div>...</div>}><PurchasePage /></Suspense>} />
          
          {/*  */}
          <Route path="/auth-test" element={<Suspense fallback={<div>...</div>}><AuthTestPage /></Suspense>} />
          

          
          {/*  -  */}
          <Route 
            path="/admin/dashboard" 
            element={
              <ProtectedRoute>
                <Suspense fallback={<div>...</div>}>
                  <AdminDashboardPage />
                </Suspense>
              </ProtectedRoute>
            } 
          />
          
          {/*  -  */}
          <Route 
            path="/admin/*" 
            element={
              <ProtectedRoute>
                <Suspense fallback={<div>...</div>}>
                  <AdminDashboardPage />
                </Suspense>
              </ProtectedRoute>
            } 
          />
          
          {/*  */}
          <Route path="/" element={<Suspense fallback={<div>...</div>}><AdminLoginPage /></Suspense>} />
          
          {/* 404 -  */}
          <Route path="*" element={
            <Suspense fallback={<div>...</div>}>
              <div style={{textAlign: 'center', padding: '50px', minHeight: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'center'}}>
                <h2></h2>
                <p></p>
              </div>
            </Suspense>
          } />
        </Routes>
      </div>
    </Router>
  );
}

export default App; 